:root {
  --active-link: #0ba5e9;
  --gradient-text: linear-gradient(90deg, rgba(11,165,233,1) 0%, rgba(252,70,107,1) 100%);
  --body-color: #10172a;
  --card-color: #1e293b;
  --text-white: #fff;
}

@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background: var(--body-color) !important;
  font-family: "Roboto", sans-serif;
}

h1,
h2 {
  color: var(--text-white);
  font-weight: 500;
  font-size: 2rem;
}

p {
  font-weight: 400;
  font-size: 1rem;
  color: rgba(255, 255, 255, 0.772);

  line-height: 35px;
}

a {
  text-decoration: none;
  color: unset;
}

a:hover {
  color: unset;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

section,
footer {
  padding: 60px 0px;
}

/* =========== custom scroll-bar ========= */

::-webkit-scrollbar {
  width: 10px;
  background: var(--card-color);
}

::-webkit-scrollbar-thumb {
  border-radius: 2em;
  background: var(--gradient-text);
}
